import React, { FormEvent, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import {
  AIwritingLayout,
  WritingTitle,
  WritingBackground,
  InputTitle,
  InputContext,
  WritingSubmitButton,
} from '../../components/aimission/styles';
import Navigation from '../../components/navigation/Navigation';

const AIwriting: React.FC = () => {
  const [title, setTitle] = useState('');
  const [context, setContext] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const navigate = useNavigate();

  const handleSubmit = async (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    const staticText =
      '    .              ,         ';
    const combinedPrompt = `${title} ${context} ${staticText}`;

    setLoading(true);
    setError(null);

    try {
      const response = await axios.get('http://localhost:8081/api/v1/main/getGptApi', {
        params: { prompt: combinedPrompt },
      });

      navigate('/mission/airesult', { state: { result: response.data.content } });
    } catch (err) {
      setError('Failed to fetch data');
      console.error('Error fetching data:', err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <AIwritingLayout>
      <WritingTitle></WritingTitle>
      <WritingBackground>
        <form onSubmit={handleSubmit}>
          <InputTitle
            id="inputTitle"
            type="text"
            placeholder=" "
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          />
          <InputContext
            id="inputContext"
            placeholder="   ,     
 )  /   /   "
            value={context}
            onChange={(e) => setContext(e.target.value)}
          />
          <WritingSubmitButton type="submit" disabled={loading}>
            {loading ? ' ...' : 'AI  !'}
          </WritingSubmitButton>
        </form>
        {error && <p>{error}</p>}
        <Navigation />
      </WritingBackground>
    </AIwritingLayout>
  );
};

export default AIwriting;
