import {
  MainBox,
  MainJob,
  MainLayout,
  MainLevelBar,
  MainLevelBox,
  MainLevelProgress,
  MainLevelText,
  MainText,
  MainTop,
} from './styles';
import { ReactComponent as Logo } from '../../assets/images/logo-s.svg';
import { ReactComponent as MissionIcon } from '../../assets/images/missionIcon.svg';
import { ReactComponent as RankingIcon } from '../../assets/images/rankingIcon.svg';
import MainSection from './MainSection';
import { useMain } from './events';

export default function MainPage() {
  const { level, progressPercentage, handleMissionList, handleJobList, ranking } = useMain();
  const nickname = sessionStorage.getItem('nickname');

  return (
    <MainLayout>
      <MainBox>
        <MainTop>
          <Logo />
          <MainText>
            <h1>{nickname}, </h1>
            <h2>  2     !</h2>
          </MainText>
          <MainLevelBox>
            <MainLevelText>
              <span>LV.{level}</span>
              <span>LV.10</span>
            </MainLevelText>
            <MainLevelBar>
              <MainLevelProgress percentage={progressPercentage}>
                <span>{progressPercentage}%</span>
              </MainLevelProgress>
            </MainLevelBar>
          </MainLevelBox>
        </MainTop>
        <MainSection Logo={MissionIcon} title=" " onClick={handleMissionList} />
        <MainSection Logo={RankingIcon} title=" !" ranking={ranking} />
        <MainJob>
          <MainSection title="  " onClick={handleJobList} />
        </MainJob>
      </MainBox>
    </MainLayout>
  );
}
