import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import {
  AIwritingLayout,
  WritingTitle,
  WritingBackground,
  AIresultBox,
  AIresultButtonBox,
  AIrewritebtn,
  AIupdatebtn,
} from '../../components/aimission/styles';
import Navigation from '../../components/navigation/Navigation';

interface LocationState {
  result: string;
}

const AIresult: React.FC = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const { result } = (location.state as LocationState) || { result: '' };

  const nickname = sessionStorage.getItem('nickname');
  const handleRewriteClick = () => {
    navigate('/mission/aiwrite');
  };

  const handleUpdateClick = () => {
    console.log(' :', result);
    navigate('/mission/aisuccess');
  };

  return (
    <AIwritingLayout>
      <WritingTitle>{nickname}     AI  !</WritingTitle>
      <WritingBackground>
        <AIresultBox>{result || ' .'}</AIresultBox>
        <AIresultButtonBox>
          <AIrewritebtn onClick={handleRewriteClick}> </AIrewritebtn>
          <AIupdatebtn onClick={handleUpdateClick}></AIupdatebtn>
        </AIresultButtonBox>
        <Navigation />
      </WritingBackground>
    </AIwritingLayout>
  );
};

export default AIresult;
